<template>
	<div class="goods-info">
		<!-- 轮播图 -->
		<div class="banner" v-if="bannerList.length">
			<el-carousel>
				<el-carousel-item v-for="item in bannerList" :key="item">
					<el-image :src="item"></el-image>
				</el-carousel-item>
			</el-carousel>
		</div>
		<!-- 商品列表 -->
		<div class="goods-list" v-if="goodsList.length">
			<goods-item v-for="goods in goodsList" :key="goods.id" :goods="goods"></goods-item>
		</div>
	</div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem';
import { getGoodsListApi, getBannerApi } from '@/api/goods';

export default {
	name: 'home',
	components: {
		GoodsItem,
	},
	data() {
		return {
			bannerList: [],
			page: 0,
			goodsList: [],
			isLoadmore: false,
		};
	},
	created() {
		this.getBanner();
		this.getGoodsList();
		document.addEventListener('scroll', this.loadmoreList);
	},
	beforeRouteLeave(to, from, next) {
		document.removeEventListener('scroll', this.loadmoreList);
		next();
	},
	methods: {
		// 获取轮播图
		async getBanner() {
			setTimeout(async () => {
				let res = await getBannerApi();
				this.bannerList = res;
			}, 1000 * Math.random());
		},
		// 获取商品列表
		async getGoodsList() {
			this.page++;
			this.isLoadmore = true;
			let res = await getGoodsListApi({ page: this.page });
			this.goodsList.push(...res);
			this.isLoadmore = false;
		},
		loadmoreList() {
			let scrollHeight = document.documentElement.scrollHeight;
			let clientHeight = document.documentElement.clientHeight;
			let scrollTop = document.scrollingElement.scrollTop;
			if (clientHeight + scrollTop + 800 >= scrollHeight && !this.isLoadmore) {
				this.getGoodsList();
			}
		},
	},
};
</script>

<style scoped>
.goods-list {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -10px;
}
</style>
